<template>
  <div ref="wrap" class="select gridWrap">
    <FilterHeader v-on:selectChange="changeIndex" :idata="selectType"></FilterHeader>
    <div ref="chart" style="width:100%;height:calc(100% - 40px)"></div>
  </div>
</template>

<script>
import FilterHeader from "./FilterHeader.vue";
import riven from "../assets/riven";

export default {
  data: function() {
    return {
      // 组件数据对象
      selectType: {
        title: "评论数据评论情感分析结果",
        selectItem: [
          {
            label: "选择类型",
            showAll: false,
            options: [
              {
                value: "50",
                label: "前50条"
              },
              {
                value: "100",
                label: "前100条"
              },
              {
                value: "200",
                label: "前200条"
              },
              {
                value: "300",
                label: "前300条"
              },
              {
                value: "500",
                label: "前500条"
              },
              {
                value: "1000",
                label: "前1000条"
              },
              {
                value: "2000",
                label: "前2000条"
              },
              {
                value: "5000",
                label: "前5000条"
              },
              {
                value: "10000",
                label: "前10000条"
              }
            ]
          }
        ]
      },
      emotionDictionaly: null,
      idata: null,
      negative_count: 0, // 消极条数
      positive_count: 0, // 积极条数
      loading: null
    };
  },
  components: {
    FilterHeader
  },
  mounted: function() {
    this.loading = this.$loading({
      lock: true,
      text: "拼命加载中",
      spinner: "el-icon-loading",
      background: "rgba(0, 0, 0, 0.7)"
    });
    this.loadDictionaly();
    this.init();
  },
  methods: {
    init: function() {
      // console.log(code);
      // init
      this.$Axios
        .get("./static/comments-sliceResult.json", {
          params: {
            v: new Date().getTime()
          }
        })
        .then(d => {
          // console.log(d);

          // 所有数据
          this.idata = d.data.data;
          this.Analysis(100);
        });
    },
    // main fun
    Analysis: function(index, change) {
      if (change) {
        this.loading = this.$loading({
          lock: true,
          text: "拼命加载中",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
      }
      // 顺利加载loading层
      setTimeout(() => {
        this.negative_count = 0;
        this.positive_count = 0;
        // 正则匹配中文
        let reg = /[\u4e00-\u9fa5]/;
        // console.log(data[0].data.split("$-$"));
        let count = index || this.idata.length;
        console.group("分析" + count + "条数据");
        console.log("分析开始：" + new Date());

        for (let a = 0; a < count; a++) {
          // for (let a = 0; a < 100; a++) {
          // 构造数组
          let cur = this.idata[a].data.split("$-$");
          // 基础分数
          let score = 0;
          // 分词处理
          let resArr = [];
          // 分组计算分数
          let scoreArr = [];
          for (let i = 0; i < cur.length; i++) {
            let c = cur[i].split(",")[0];
            // 词库分析
            let res = this.ComparedWord(c);
            // console.log(res);
            // 存入结果数组
            resArr.push(res);
            // 不存在分析结果，跳过不处理
            if (res.type == "noFound") {
              // 如果最后一个noFound 计算分数
              if (i == cur.length - 1) {
                scoreArr.push(score);
              }
              continue;
            }
            // 如果是标点符号，分组计算分数
            if (res.type == "mark") {
              scoreArr.push(score);
              score = 0;
            }
            // console.log(resArr);

            // console.log(resArr[i - 1], i);

            // 当前结果属于积极 评价/情感词
            if (res.type == "pos_emotion" || res.type == "pos_evaluate") {
              score = score + res.score;
              // 两种情况：
              // 1. 非常 不 好吃
              // 2. 不是 很 好吃
              // 需要极性反转
              // 是否需要极性反转 前一个或者前两个
              if (
                (i - 1 >= 0 &&
                  resArr[i - 1].type &&
                  resArr[i - 1].type == "neg_degree") ||
                (i - 2 >= 0 &&
                  resArr[i - 2].type &&
                  resArr[i - 2].type == "neg_degree")
              ) {
                score = score * -1;
                if (
                  i - 1 >= 0 &&
                  resArr[i - 1].type &&
                  resArr[i - 1].type == "neg_degree" &&
                  (i - 2 >= 0 &&
                    resArr[i - 2].type &&
                    resArr[i - 2].type == "neg_degree")
                ) {
                  score = score * -1;
                }
              }
              // 当前结果属于消极 评价/情感词
            } else if (
              res.type == "neg_emotion" ||
              res.type == "neg_evaluate"
            ) {
              score = score + res.score;
              // 1. 不是 不好
              // 2. 不是 很 不好
              // 极性反转
              // 是否需要极性反转 前一个或者前两个
              if (
                (i - 1 >= 0 &&
                  resArr[i - 1].type &&
                  resArr[i - 1].type == "neg_degree") ||
                (i - 2 >= 0 &&
                  resArr[i - 2].type &&
                  resArr[i - 2].type == "neg_degree")
              ) {
                score = score * -1;
                if (
                  i - 1 >= 0 &&
                  resArr[i - 1].type &&
                  resArr[i - 1].type == "neg_degree" &&
                  (i - 2 >= 0 &&
                    resArr[i - 2].type &&
                    resArr[i - 2].type == "neg_degree")
                ) {
                  score = score * -1;
                }
              }
            }
            // 当前结果属于程度词  相乘 #赫夫曼二叉树， 加权路径最小
            if (i - 1 >= 0) {
              if (
                resArr[i - 1].type == "most_degree" ||
                (i - 2 >= 0 && resArr[i - 2].type == "most_degree")
              ) {
                score = score * 3;
              } else if (
                resArr[i - 1].type == "very_degree" ||
                (i - 2 >= 0 && resArr[i - 2].type == "very_degree")
              ) {
                score = score * 2.5;
              } else if (
                resArr[i - 1].type == "more_degree" ||
                (i - 2 >= 0 && resArr[i - 2].type == "more_degree")
              ) {
                score = score * 2;
              } else if (
                resArr[i - 1].type == "ish_degree" ||
                (i - 2 >= 0 && resArr[i - 2].type == "ish_degree")
              ) {
                score = score * 1.5;
              } else if (
                resArr[i - 1].type == "least_degree" ||
                (i - 2 >= 0 && resArr[i - 2].type == "least_degree")
              ) {
                score = score * 1.1;
              }
            }
            // 如果是最后一组
            if (i == cur.length - 1) {
              scoreArr.push(score);
            }
            // console.log(score);
          }
          // console.log(scoreArr);
          let lastScore = 0;
          for (let i = 0; i < scoreArr.length; i++) {
            lastScore += scoreArr[i];
          }

          // console.log("分数为：" + lastScore);
          if (lastScore > 0) {
            this.positive_count++;
          } else {
            this.negative_count++;
          }
        }
        console.log("积极：" + this.positive_count);
        console.log("消极：" + this.negative_count);

        console.log("分析结束：" + new Date());
        console.groupEnd();
        let opt = {
          textColor: "#fff",
          title: count + "条评论情感倾向统计分析",
          legendData: ["情感倾向于积极的评论个数", "情感倾向于消极的评论个数"],
          seriesName: "情感倾向统计分析",
          seriesData: [
            {
              name: "情感倾向于积极的评论个数",
              value: this.positive_count
            },
            {
              name: "情感倾向于消极的评论个数",
              value: this.negative_count
            }
          ]
        };
        riven.initChart(this, opt);
        this.loading.close();
      }, 100);
    },
    loadDictionaly: function() {
      this.$Axios
        .get("./static/emotion.json", {
          params: {
            v: new Date().getTime()
          }
        })
        .then(d => {
          // console.log(d);
          this.emotionDictionaly = d.data.data;
          // console.log(this.emotionDictionaly);
        });
    },
    ComparedWord: function(word) {
      // 正则匹配中文
      let reg = /[\u4e00-\u9fa5]/;
      // 不是中文
      if (!reg.test(word)) {
        return {
          word: word,
          type: "mark"
        };
      }
      // 遍历词典 一共10小本
      let obj = {}; // 结果对象
      if (!this.emotionDictionaly) {
        this.$message("情感词典还未加载完成,请重试");
        return 0;
      }
      // 大词典 this.emotionDictionaly
      let len = this.emotionDictionaly.length;
      for (let i = 0; i < len; i++) {
        let dArr = this.emotionDictionaly[i]; // 当前词典
        // 遍历小词典
        for (let j = 0; j < dArr.degree_data.length; j++) {
          let c = dArr.degree_data[j];

          if (c == word) {
            return {
              word: word,
              type: dArr.desc,
              score: dArr.value
            };
          }
        }
      }
      return {
        word: word,
        type: "noFound"
      };
    },
    changeIndex: function(code) {
      // console.log(code);
      if (code.length > 0) {
        console.log("filter" + code);
        this.Analysis(code, "change");
      }
    }
  }
};
</script>

<style scoped>
/* reset */
.el-select.el-select--mini {
  margin: 8px 5px;
}
.select {
  box-sizing: border-box;
  /* padding: 5px; */
}
.scrollWrap {
  /* margin-bottom: 20px; */
  /* max-height: 240px; */
  max-height: calc(100% - 77px);
  overflow-y: scroll;
  /* overflow-x: hidden; */
}
</style>



