export default {
    initChart: function(_this, options) {
        _this.chart = _this.$echarts.init(_this.$refs.chart);

        let default_color = ['#c23531', '#2f4554', '#61a0a8', '#d48265', '#91c7ae', '#749f83', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'];

        let option = {
            title: {
                text: options.title,
                x: "center",
                textStyle: {
                    color: options.textColor || "#333"
                }
            },
            // color: [
            //     "#7cb5ec",
            //     "#434348",
            //     "#90ed7d",
            //     "#f7a35c",
            //     "#8085e9",
            //     "#f15c80",
            //     "#e4d354",
            //     "#2b908f",
            //     "#f45b5b",
            //     "#91e8e1"
            // ],
            color: options.color || default_color,
            tooltip: {
                trigger: "item",
                formatter: "{a} <br/>{b} : {c}个 ({d}%)"
            },
            legend: {
                orient: "vertical",
                left: "left",
                data: options.legendData,
                textStyle: {
                    color: options.textColor || "#333"
                }
            },
            textStyle: {
                color: options.textColor || "#333"
            },
            series: [{
                name: options.seriesName,
                type: "pie",
                radius: "55%",
                center: ["50%", "60%"],
                data: options.seriesData, // must
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: "rgba(0, 0, 0, 0.5)"
                    }
                }
            }]
        };
        _this.chart.setOption(option);
    },
};


