<template>
  <div ref="wrap" class="select gridWrap">
    <FilterHeader v-on:selectChange="changeIndex" :idata="selectType"></FilterHeader>
    <div ref="chart" style="width:100%;height:calc(100% - 40px)"></div>
  </div>
</template>

<script>
import FilterHeader from "./FilterHeader.vue";
import riven from "../assets/riven";

export default {
  data: function() {
    return {
      // 组件数据对象
      selectType: {
        title: "评论数据评价星级统计分析",
        selectItem: [
          {
            label: "选择类型",
            showAll: false,
            options: [
              {
                value: "50",
                label: "前50条"
              },
              {
                value: "100",
                label: "前100条"
              },
              {
                value: "200",
                label: "前200条"
              },
              {
                value: "300",
                label: "前300条"
              },
              {
                value: "500",
                label: "前500条"
              },
              {
                value: "1000",
                label: "前1000条"
              },
              {
                value: "2000",
                label: "前2000条"
              },
              {
                value: "5000",
                label: "前5000条"
              },
              {
                value: "10000",
                label: "前10000条"
              }
            ]
          }
        ],
        idata: null,
        loading: null
      }
    };
  },
  components: {
    FilterHeader
  },
  mounted: function() {
    this.init();
  },
  methods: {
    init: function() {
      // console.log(code);
      // init
      this.$Axios
        .get("./static/comments.json", {
          params: {
            v: new Date().getTime()
          }
        })
        .then(d => {
          // console.log(d);
          this.idata = d.data.data.comments;
          this.starCount(100);
        });
    },
    starCount: function(index, change) {
      if (change) {
        this.loading = this.$loading({
          lock: true,
          text: "拼命加载中",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
      }
      let pos_count = 0,
        neg_count = 0;
      let count = index || this.idata.length;
      for (let i = 0; i < count; i++) {
        let c = this.idata[i];
        if (c.star < 30) {
          neg_count++;
        } else {
          pos_count++;
        }
      }
      // console.log("小于30：" + neg_count);
      // console.log("大于等于30：" + pos_count);
      let opt = {
        textColor: "#fff",
        title: count + "条评论星级统计",
        legendData: ["星级大于等于3", "星级小于3"],
        seriesName: "星级统计分析",
        seriesData: [
          {
            name: "星级大于等于3",
            value: pos_count
          },
          {
            name: "星级小于3",
            value: neg_count
          }
        ],
        color: [
          "#7cb5ec",
          "#434348",
          "#90ed7d",
          "#f7a35c",
          "#8085e9",
          "#f15c80",
          "#e4d354",
          "#2b908f",
          "#f45b5b",
          "#91e8e1"
        ]
      };
      riven.initChart(this, opt);
      if (change) {
        this.loading.close();
      }
    },
    changeIndex: function(code) {
      // console.log(code);
      if (code.length > 0) {
        console.log("filter" + code);
        this.starCount(code, "change");
      }
    }
  }
};
</script>

<style scoped>
/* reset */
.el-select.el-select--mini {
  margin: 8px 5px;
}
.select {
  box-sizing: border-box;
  /* padding: 5px; */
}
.scrollWrap {
  /* margin-bottom: 20px; */
  /* max-height: 240px; */
  max-height: calc(100% - 77px);
  overflow-y: scroll;
  /* overflow-x: hidden; */
}
</style>



