<template>
  <!-- header -->
  <div class="titleWrap">
    <div class="fh_title">{{idata.title}}</div>
    <div class="fh_dd">
      <el-dropdown trigger="click" :hide-on-click="false">
        <span class="el-dropdown-link">
          <i class="el-icon-setting fh_menu"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-for="(item,index) in idata.selectItem" :key="index">
            <!-- <div> -->
            <span class="demonstration" style="margin-right:15px;">{{item.label}} :</span>
            <el-select v-model="default_select[index]" size="mini" :placeholder="'请'+item.label" @change="emitData()">
              <el-option label="所有" value="all" v-if="item.showAll">
              </el-option>
              <el-option v-for="option in item.options" :key="option.value" :label="option.label" :value="option.value">
              </el-option>
            </el-select>
            <!-- </div> -->
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      default_select: [] // 默认选中数据数组
    };
  },
  props: {
    idata: {
      type: Object,
      default: function() {
        return {
          title: "标题",
          selectItem: [
            {
              label: "选择",
              showAll: true,
              options: []
            }
          ]
        };
      }
    }
  },
  mounted: function() {
    this.emitData();
  },
  methods: {
    emitData: function() {
      // console.log(this.default_select);
      // console.log(code);
      this.$emit("selectChange", this.default_select);
    },
    test(e) {
      console.log(e);
      e.stopPropagation();
      e.preventDefault();
    }
  }
};
</script>

<style scoped>
.titleWrap {
  width: 100%;
  height: 40px;
  background-color: #ccc;
  box-sizing: border-box;
}
.fh_title {
  position: relative;
  width: 100%;
  height: 40px;
  font-size: 18px;
  text-indent: 10px;
  color: #333;
  line-height: 40px;
  padding-right: 50px;
  box-sizing: border-box;
}
.fh_dd {
  position: absolute;
  right: 8px;
  top: 5px;
  box-sizing: border-box;
}
.fh_dd .fh_menu {
  font-size: 30px;
  color: #333;
  cursor: pointer;
}
</style>



