<template>
  <div style="width:100%;height:100%">
    <FilterHeader v-on:selectChange="changeIndex" :idata="selectType"></FilterHeader>
    <div ref="chart" style="width:100%;height:calc(100% - 40px)"></div>
  </div>
</template>

<script>
import riven from "../assets/riven";
import FilterHeader from "./FilterHeader.vue";

export default {
  data: function() {
    return {
      // 组件数据对象
      selectType: {
        title: "评论数据人为情感评定",
        selectItem: [
          {
            label: "选择类型",
            showAll: false,
            options: [
              {
                value: "50",
                label: "前50条"
              },
              {
                value: "100",
                label: "前100条"
              },
              {
                value: "200",
                label: "前200条"
              }
            ]
          }
        ]
      },
      chart: null,
      idata: null,
      oloading: null
    };
  },
  components: {
    FilterHeader
  },
  mounted: function() {
    this.init();
  },
  methods: {
    init: function() {
      // init
      this.$Axios
        .get("./static/comment200.json", {
          params: {
            v: new Date().getTime()
          }
        })
        .then(d => {
          // console.log(d);
          this.idata = d.data.data.comments;
          this.oIdeaCount(100);
        });
    },
    oIdeaCount: function(index, change) {
      if (change) {
        this.oloading = this.$loading({
          lock: true,
          text: "拼命加载中",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
      }
      let pos_count = 0,
        neg_count = 0;
      let count = index || this.idata.length;
      for (let i = 0; i < count; i++) {
        let c = this.idata[i];
        if (c.oIdea < 1) {
          neg_count++;
        } else {
          pos_count++;
        }
      }
      // console.log("小于30：" + neg_count);
      // console.log("大于等于30：" + pos_count);
      let opt = {
        textColor: "#fff",
        title: count + "条人为情感评定",
        legendData: ["积极情感个数", "消极情感个数"],
        seriesName: "人为情感评定",
        seriesData: [
          {
            name: "积极情感个数",
            value: pos_count
          },
          {
            name: "消极情感个数",
            value: neg_count
          }
        ],
        color: [
          "#7cb5ec",
          "#434348",
          "#90ed7d",
          "#f7a35c",
          "#8085e9",
          "#f15c80",
          "#e4d354",
          "#2b908f",
          "#f45b5b",
          "#91e8e1"
        ]
      };
      riven.initChart(this, opt);
      if (change) {
        this.oloading.close();
      }
    },
    changeIndex: function(code) {
      console.log(code);
      if (code.length > 0) {
        console.log("filter" + code);
        this.oIdeaCount(code, "change");
      }
    }
  }
};
</script>

<style scoped>

</style>



