<template>
  <div style="width:100%;height:100%">
    <!-- <FilterHeader v-on:selectChange="changeIndex" :idata="selectType"></FilterHeader> -->
    <div ref="chart" style="width:100%;height:calc(100% - 40px)"></div>
  </div>
</template>

<script>
import riven from "../assets/riven";

export default {
  data: function() {
    return {
      chart: null,
      idata: null
    };
  },
  mounted: function() {
    this.$Axios
      .get("./static/comments.json", {
        params: {
          v: new Date().getTime()
        }
      })
      .then(d => {
        console.log(d);
        this.idata = d.data.data.tags;
        let legend = [],
          series = [],
          allTags = 0;
        for (let i = 0; i < this.idata.length; i++) {
          let c = this.idata[i];
          allTags += c.count;
          legend.push(c.tag);
          series.push({
            name: c.tag,
            value: c.count
          });
        }
        let opt = {
          textColor: "#fff",
          title: "餐厅标签,共" + allTags + "个",
          legendData: legend,
          seriesName: "餐厅标签",
          seriesData: series,
          color: [
            "#7cb5ec",
            "#434348",
            "#90ed7d",
            "#f7a35c",
            "#8085e9",
            "#f15c80",
            "#e4d354",
            "#2b908f",
            "#f45b5b",
            "#91e8e1"
          ]
        };
        riven.initChart(this, opt);
      });
  }
};
</script>

<style scoped>

</style>



