<template>
  <div id="app" class="layoutWrap" ref="layoutWrap">
      <grid-layout v-if="options" :layout="options.data" :auto-size="true" :col-num="options.colNum" :margin="[5, 5]" :row-height="rowHeight" :is-draggable="options.isDraggable" :is-resizable="options.isResizable" @layout-updated="layoutUpdatedEvent">
        <grid-item v-for="(item,index) in options.data" :key="index" :x="item.x" :y="item.y" :w="item.w" :h="item.h" :i="item.i">
          <keep-alive>
            <component :is="item.component" :chartData="item.data" :dateArea="dateArea" :size="[item.w*(rowWidth+5)-5,item.h*(rowHeight+5)-5]"></component>
          </keep-alive>
        </grid-item>
      </grid-layout>
  </div>
</template>

<script>
import { bus } from "./assets/bus.js";
import VueGridLayout from "vue-grid-layout";
import component1 from "./components/LevelStar.vue";
import component2 from "./components/Tag.vue";
import component3 from "./components/PeopleCount.vue";
import component4 from "./components/AnalysisResult.vue";

export default {
  data: function() {
    return {
      state: false,
      options: null,
      wrapHeight: 100,
      wrapWidth: 100,
      dateArea: {
        areaCode: "530000",
        dateRange: []
      }
    };
  },
  components: {
    GridLayout: VueGridLayout.GridLayout,
    GridItem: VueGridLayout.GridItem,
    component1,
    component2,
    component3,
    component4
  },
  computed: {
    rowHeight: function() {
      let maginX = this.margin ? this.margin[1] : 5,
        rowNum = this.options.rowNum;
      return (this.wrapHeight - maginX * (rowNum + 1)) / rowNum;
    },
    rowWidth: function() {
      let maginX = this.margin ? this.margin[1] : 5,
        colNum = this.options.colNum;
      return (this.wrapWidth - maginX * (colNum + 1)) / colNum;
    }
  },
  created: function() {
    // get layout data
    this.$Axios.get("./static/layout.json").then(d => {
      // console.log(d);
      this.options = d.data;
    });
  },
  mounted: function() {
    // init width height
    let content = this.$refs.layoutWrap;
    this.wrapWidth = content.offsetWidth;
    this.wrapHeight = content.offsetHeight;
    window.onresize = () => {
      this.wrapWidth = content.offsetWidth;
      this.wrapHeight = content.offsetHeight;
    };
  },
  methods: {
    changebox: function() {
      this.options.isDraggable = !this.options.isDraggable;
      this.options.isResizable = !this.options.isResizable;
      this.state = !this.state;
    },
    layoutUpdatedEvent: function(newLayout) {
      // console.log("change grid");
      // console.log(newLayout);
      bus.$emit("resizeEnd");
    }
  }
};
</script>

<style>
@import url(./assets/base.css);
@import url(./assets/theme-black.css);
#app,
html,
body {
  height: 100%;
}
body {
  margin: 0;
  background-color: #e0e0e0;
}
.cont {
  width: 100%;
  height: calc(100% - 50px);
}
/* public */
.gridWrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
/* table common */
.table_common {
  width: 100%;
  /* padding-left: 5px; */
  color: #606266;
}
.table_common tr {
  height: 35px;
}
.table_common tr td,
.table_common tr th {
  height: 35px;
  text-align: center;
  font-size: 14px;
}
/* element reset */
.el-tabs .el-tabs__header {
  margin-bottom: 5px !important;
}
.el-icon-loading {
  font-size: 24px !important;
}
.layoutWrap {
  display: block;
  width: 100%;
  height: 100%;
}
.vue-grid-item {
  /* background-color: rgba(41, 61, 89, 0.9); */
  border: 1px solid #ccc;
  /* color: #fff; */
  background-color: #fff;
  /* background-color: #2f3336; */
}
</style>



